<template>
  <div>
    <div class="header">
      <div class="back" @click="back">
        <img src="./back.png" alt="">
      </div>
      订单详情
    </div>
    <div class="odetail-contain">
      <div class="a-infor">
        <div class="p">
          <span>收货人</span>
          <em>{{infor.rname}}</em>
          <em>{{infor.phone}}</em>
        </div>
        <div class="p p1">
          提货地址:<b>{{infor.address}}</b>
        </div>
      </div>
      <ul class="list">
        <div class="title">
          {{infor.create_time}}
        </div>
        <li v-for="item in infor.goods_info">
          <div class="con">
            <img :src="item.gimg" alt="">
            <div class="dd">
              <h3>{{item.gname}}</h3>
              <p>
                <span>￥{{item.price}}</span>
                ×{{item.num}}
              </p>
            </div>
          </div>
        </li>
      </ul>
      <div class="odetail-i">
        <p>订单编号：{{infor.order_no}}</p>
        <p>创建时间：{{infor.create_time}}</p>
        <p>付款时间：{{infor.pay_time}}</p>
      </div>
    </div>
    <div class="odetail-bottom" @click="sure" v-if="infor.is_send == 0">已取货</div>
  </div>
</template>
<script>
  export default{
    data(){
      return {
        id:'',
        infor: ''
      }
    },
    created(){
      this.id = this.$route.query.id;
      let obj = this.doQs({
        time: TIMER,
        id: this.id,
        hash: '39a851a7218472226ece36d1377f3cf0'
      });
      this.$axios.post(APIURL + 'Api/Business/orderDetail',obj).then((res)=>{
        console.log(res)
        if(res.data.Code == 0){
          this.infor = res.data.Data;
        }else{
          this.webTip(res.data.Msg);
        }
      })
    },
    methods:{
      back(){
        this.$router.go(-1);
      },
      sure(){
        let $this = this;
        let obj = this.doQs({
          time: TIMER,
          id: this.id,
          hash: '39a851a7218472226ece36d1377f3cf0'
        });
        this.$axios.post(APIURL + 'Api/Business/sendMessage',obj).then((res)=>{
          console.log(res)
          if(res.data.Code == 0){
            this.webTip('操作成功',function(){
              location.reload();
            });
          }else{
            this.webTip(res.data.Msg);
          }
        })
      },
      cancel(){
        let $this = this;
        let obj = this.doQs({
          time: TIMER,
          uid: loginInfor.uid,
          hashid: loginInfor.hashid,
          id: this.id,
          hash: '3435103f11f21993476fe72ff7a20b8f'
        });
        this.$axios.post(APIURL + 'Api/Order/orderCancel',obj).then((res)=>{
          console.log(res)
          if(res.data.Code == 0){
            this.webTip('取消成功',function(){
              $this.$router.go(-1);
            });
          }else{
            this.webTip(res.data.Msg);
          }
          
        })
      }
    }
  }
</script>
<style scoped>
  .odetail-contain{
    padding-top: 0.9rem;
    padding-bottom: 1rem;
  }
  .a-infor .p{
    padding: 0.3rem;
    background: #fff;
    color: #999;
    line-height: 0.4rem;
    text-align: justify;
  }
  .a-infor .p span{
    vertical-align: middle;
  }
  .a-infor .p em{
    display: inline-block;
    width: 2.4rem;
    height: 0.4rem;
    border: solid 1px #ddd;
    border-radius: 3px;
    line-height: 0.4rem;
    padding: 0 0.2rem;
    margin-left: 0.1rem;
    box-sizing: border-box;
    vertical-align: middle;
    color: #000;
  }
  .a-infor .p b{
    color: #000;
  }
  .a-infor .p1{
    margin-top: -0.3rem;
  }
  .list{
    margin-top: 0.1rem;
    background: #fff;
  }
  .list li{
    padding: 0 0.3rem;
    position: relative;
  }
  .list .title{
    line-height: 0.6rem;
    position: relative;
    color: #999;
    padding-left: 0.3rem;
  }
  .list li .title .status{
    float: right;
    color: #98c347;
  }
  .list .title:after{
    position: absolute;
    left: 0;
    bottom: -1px;
    width: 100%;
    height: 1px;
    border-top:solid 1px #eee;
    transform: scaleY(0.5);
    content: '';
  }
  .list li .con{
    display: flex;
    padding: 0.2rem 0;
    height: 1.5rem;
    position: relative;
  }
  .list li .con:after{
    position: absolute;
    left: 0;
    bottom: -1px;
    width: 100%;
    height: 1px;
    border-top:solid 1px #eee;
    transform: scaleY(0.5);
    content: '';
  }
  .list li .con img{
    width: 1.5rem;
    height: 1.5rem;
    flex: 0 0 1.5rem;
  }
  .list li .con .dd{
    flex: 1;
    padding-left: 0.2rem;
  }
  .list li .con .dd h3{
    line-height: 0.4rem;
    font-size: 15px;
    font-weight: 500;
    height: 0.8rem;
    margin-top: 0.1rem;
  }
  .list li .con .dd p{
    color: #999;
    font-size: 15px;
  }
  .list li .con .dd p span{
    color: #e10000;
    font-weight: 500;
    float: right;
  }
  .odetail-i{
    padding: 0.2rem 0.3rem;
    margin-top: 0.1rem;
    background: #fff;
    color: #999;
  }
  .odetail-bottom{
    position: fixed;
    left: 0;
    bottom: 0;
    background: #98c347;
    width: 100%;
    height: 0;
    z-index: 99;
    font-size: 16px;
    text-align: center;
    line-height: 0;
    padding: 0.5rem 0;
    color: #fff;
  }

  .header{
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 0;
    background: #fff;
    z-index: 99;
    text-align: center;
    line-height: 0;
    font-size: 16px;
    box-shadow: 0 0 1px #ddd;
    padding: 0.45rem 0;
    font-weight: 500;
  }
  .header .back{
    position: absolute;
    left: 0;
    top: 0;
    width: 0.44rem;
    height: 0.44rem;
    padding: 0.23rem;
  }
  .header .back>img{
    width: 100%;
    height: 100%;
  }
</style>


