<template>
  <div class="l-page">
    <h3 class="l-title">登陆</h3>
    <div class="login-text">欢迎来到立刻购物商城</div>
    <div class="l-box">
      <input type="tel" placeholder="请输入您的手机号" v-model="user">
    </div>
    <div class="l-box">
      <input type="password" placeholder="请输入密码" v-model="pw">
    </div>
    <button type="button" class="l-btn" @click="submit">登陆</button>
  </div>
</template>
<script>
  export default{
    data(){
      return {
        user: '',
        pw: ''
      }
    },
    methods:{
      submit(){
        if(!this.user){
          this.webTip('请输入您的账号');
          return false;
        }
        if(!this.pw){
          this.webTip('请输入密码');
          return false;
        }
        let obj = this.doQs({
          time: TIMER,
          username: this.user,
          password: this.pw,
          hash: '3c437a1b469dc67c1e1a804b3a00270b'
        })
        this.$axios.post(APIURL + 'Api/Business/login',obj).then((res)=>{
          let $this = this;
          if(res.data.Code == 0){
            let infor = res.data.Data;
            localStorage.setItem('shopLogin',JSON.stringify(infor));
            this.webTip('登陆成功',function(){
              $this.$router.push('/index');
            })
          }else{
            this.webTip(res.data.Msg);
          }
        })
      }
    }
  }
</script>
<style scoped>
  .l-page{
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    padding: 0 0.4rem;
    background: #fff;
  }
  .l-title{
    font-size: 24px;
    color: #000;
    margin-top: 2.2rem;
  }
  .login-text{
    margin-top: 0.2rem;
    color: #666;
    margin-bottom: 0.7rem;
  }
  .l-box{
    margin-top: 0.4rem;
    position: relative;
    padding: 0.15rem 0;
  }
  .l-box:after{
    position: absolute;
    left: 0;
    bottom: -1px;
    width: 100%;
    height: 1px;
    border-top: solid 1px #ddd;
    transform: scaleY(0.5);
    content: '';
  }
  .l-box input{
    width: 100%;
    line-height: 0.45rem;
    margin: 0;
    padding: 0;
    appearance: none;
    border: none;
    font-size: 14px;
  }
  .l-btn{
    width: 100%;
    height: 0.9rem;
    background: #98c347;
    color: #fff;
    border: none;
    border-radius: 0.1rem;
    margin-top: 1rem;
    font-size: 15px;
  }
</style>


