<template>
  <div>
    <div class="infor">
      <img src="./my.png" alt="" class="b-img">
      <div class="con">
        <img :src="infor.face" alt="" class="avatar">
        <div class="ct">
          <h3>{{infor.name}}</h3>
        </div>
        <router-link to="/search">
          <img src="./f.png" alt="" class="fdj">
        </router-link>
      </div>
    </div>
    <div class="menu">
      <div class="item" :class="{on: tab == 0}" @click="change(0)">今日订单</div>
      <div class="item" :class="{on: tab == 1}" @click="change(1)">历史订单</div>
    </div>
    <div class="list-contain">
      <scroller class="list-box" v-show="tab == 0">
        <ul class="list">
          <li v-for="item in list1">
            <router-link :to="'/odetail?id='+item.id">
              <div class="title">
                <div class="status" v-if="item.is_send == 0">未取货</div>
                <div class="status" v-else>已取货</div>
                {{item.create_time}}
              </div>
              <div class="con" v-for="con in item.sublist">
                <img :src="con.gimg" alt="">
                <div class="dd">
                  <h3>{{con.gname}}</h3>
                  <p>
                    <span>￥{{con.price}}</span>
                    ×{{con.num}}
                  </p>
                </div>
              </div>
              </router-link>
              <div class="option" v-if="item.is_send == 0">
                <div class="item">
                  <span @click="sure(item.id)">已取货</span>
                </div>
              </div>
          </li>
        </ul>
      </scroller>
      <scroller class="list-box" v-show="tab == 1">
        <ul class="list">
          <li v-for="item in list2">
            <router-link :to="'/odetail?id='+item.id">
              <div class="title">
                <div class="status" v-if="item.is_send == 0">未取货</div>
                <div class="status" v-else>已取货</div>
                {{item.create_time}}
              </div>
              <div class="con" v-for="con in item.sublist">
                <img :src="con.gimg" alt="">
                <div class="dd">
                  <h3>{{con.gname}}</h3>
                  <p>
                    <span>￥{{con.price}}</span>
                    ×{{con.num}}
                  </p>
                </div>
              </div>
              </router-link>
              <div class="option" v-if="item.is_send == 0">
                <div class="item">
                  <span @click="sure(item.id)">已取货</span>
                </div>
              </div>
          </li>
        </ul>
      </scroller>
    </div>
    
  </div>
</template>
<script>
  export default{
    data(){
      return{
        tab: 0,
        shopId: '',
        infor:{},
        list1:[],
        list2:[]
      }
    },
    created(){
      this.infor= JSON.parse(localStorage.getItem('shopLogin'));
      this.getData(1);
      this.getData(2);
    },
    methods:{
      change(index){
        if(index == this.tab){
          return false;
        }
        this.tab = index;
      },
      getData(type){
        let obj = this.doQs({
          time: TIMER,
          shop_id: this.infor.shop_id,
          type: type,
          keyword: '',
          page: 1,
          hash: '99310bc7b00cec5d6a660ad70741a1b2'
        })
        this.$axios.post(APIURL + 'Api/Business/orderList',obj).then((res)=>{
          console.log(res)
          let $this = this;
          if(res.data.Code == 0){
            if(type == 1){
              this.list1 = res.data.Data.list;
            }else{
              this.list2 = res.data.Data.list;
            }
          }else{
            this.webTip(res.data.Msg);
          }
        })
      },
      sure(id){
        let $this = this;
        let obj = this.doQs({
          time: TIMER,
          id: id,
          hash: '39a851a7218472226ece36d1377f3cf0'
        });
        this.$axios.post(APIURL + 'Api/Business/sendMessage',obj).then((res)=>{
          console.log(res)
          if(res.data.Code == 0){
            this.webTip('操作成功',function(){
              $this.getData(1);
              $this.getData(2);
            });
          }else{
            this.webTip(res.data.Msg);
          }
        })
      },
    }
  }
</script>
<style scoped>
.infor{
  position: relative;
}
.b-img{
  width: 100%;
  height: 3rem;
}
.infor .con{
  width: 6.4rem;
  height: 1.2rem;
  position: absolute;
  left: 0.6rem;
  top: 50%;
  margin-top: -0.6rem;
  display: flex;
}
.infor .con img{
  flex: 0 0 1.2rem;
  width: 1.2rem;
  height: 1.2rem;
  border-radius: 50%;
  border: solid 1px #fff;
  box-sizing: border-box;
}
.infor .con .ct{
  flex: 1;
  padding-left: 0.2rem;
}
.infor .con .ct h3{
  color: #fff;
  font-size: 18px;
  margin-top: 0.1rem;
}
.infor .con img.fdj{
  position: absolute;
  right: 0;
  top: 0.15rem;
  width: 0.48rem;
  height: 0.48rem;
  border: none;
}
.menu{
  height: 0.7rem;
  background: #fff;
  text-align: center;
  display: flex;
}
.menu .item{
  flex: 1;
  padding: 0.35rem 0;
  line-height: 0;
  height: 0;
  color: #666;
  position: relative;
}
.menu .item.on{
  color: #98c347;
}
.menu .item.on:after{
  display: block;
}
.menu .item:after{
  width: 0.6rem;
  height: 2px;
  background: #98c347;
  position: absolute;
  left: 50%;
  bottom: 0;
  content: '';
  margin-left: -0.3rem;
  display: none;
}
.list-contain{
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  top: 3.7rem;
}
.list li{
  padding: 0 0.3rem;
  background: #fff;
  position: relative;
  margin-top: 0.1rem;
}
.list li a{
  display: block;
}
.list li .title{
  line-height: 0.6rem;
  position: relative;
  color: #999;
}
.list li .title .status{
  float: right;
  color: #98c347;
}
.list li .title:after{
  position: absolute;
  left: 0;
  bottom: -1px;
  width: 100%;
  height: 1px;
  border-top:solid 1px #eee;
  transform: scaleY(0.5);
  content: '';
}
.list li .con{
  display: flex;
  padding: 0.2rem 0;
  height: 1.5rem;
  position: relative;
}
.list li .con img{
  width: 1.5rem;
  height: 1.5rem;
  flex: 0 0 1.5rem;
}
.list li .con .dd{
  flex: 1;
  padding-left: 0.2rem;
}
.list li .con .dd h3{
  line-height: 0.4rem;
  font-size: 15px;
  font-weight: 500;
  height: 0.8rem;
  margin-top: 0.1rem;
  overflow-y: hidden;
}
.list li .con .dd p{
  color: #999;
  font-size: 15px;
}
.list li .con .dd p span{
  color: #e10000;
  font-weight: 500;
  float: right;
}
.list li .option{
  position: relative;
  padding: 0.2rem 0;
  text-align: right;
  font-size: 0;
  height: 0.5rem;
}
.list li .option:after{
  position: absolute;
  left: 0;
  top: -1px;
  width: 100%;
  height: 1px;
  border-top:solid 1px #ddd;
  transform: scaleY(0.5);
  content: '';
}
.list li .option .item{
  display: inline-block;
  width: 1.5rem;
  height: 0.5rem;
  margin-left: 0.2rem;
}
.list li .option .item span{
  display: block;
  width: 200%;
  height: 200%;
  font-size: 26px;
  border: solid 1px #a0c759;
  box-sizing: border-box;
  border-radius: 0.5rem;
  transform: scale(0.5) translate(-50%,-50%);
  text-align: center;
  line-height: 1rem;
  color: #a0c759;
}
.list li .option .item span.cancel{
  color: #999;
  border-color: #999;
}
.no-data{
  color: #999;
  text-align: center;
  line-height: 0.4rem;
  margin-top: 0.5rem;
}
</style>


