<template>
  <div>
    <div class="search-header">
      <div class="s-back" @click="back">
        <img src="./back.png" alt="">
      </div>
      <div class="search-box">
        <form action="" ref="form" @submit="search">
          <input type="search" class="s-input" placeholder="输入关键词搜索您想要的商品" v-model="key" ref="input">
        </form>
        <img src="./search.png" alt="" class="s-dot">
      </div>
    </div>
    <div class="search-contain">
      <ul class="list" v-show="!noData">
        <li v-for="item in list">
            <router-link :to="'/odetail?id='+item.id">
            <div class="title">
              <div class="status" v-if="item.status == 1">未取货</div>
                <div class="status" v-else>已取货</div>
                {{item.create_time}}
            </div>
            <div class="con" v-for="con in item.sublist">
              <img :src="con.gimg" alt="">
              <div class="dd">
                <h3>{{con.gname}}</h3>
                <p>
                  <span>￥{{con.price}}</span>
                  ×{{con.num}}
                </p>
              </div>
            </div>
            </router-link>
            <!-- <div class="option" v-if="item.status == 1">
              <div class="item">
                <span @click="sure">已取货</span>
              </div>
            </div> -->
        </li>
      </ul>
      <div class="no-data" v-show="noData">
        暂无相关商品
      </div>
    </div>
  </div>
</template>
<script>
  export default{
    data(){
      return {
        noData: false,
        key: '',
        list:[],
        infor:[]
      }
    },
    created(){
      this.infor= JSON.parse(localStorage.getItem('shopLogin'));
    },
    methods:{
      back(){
        this.$router.push('/index');
      },
      search(){
        let obj = this.doQs({
          time: TIMER,
          shop_id: this.infor.shop_id,
          type: '',
          keyword: this.key,
          page: 1,
          hash: '99310bc7b00cec5d6a660ad70741a1b2'
        })
        this.$axios.post(APIURL + 'Api/Business/orderList',obj).then((res)=>{
          console.log(res)
          let $this = this;
          if(res.data.Code == 0){
            this.list = res.data.Data.list;
          }else{
            this.webTip(res.data.Msg);
          }
        })
      }
    }
  }
</script>
<style scoped>
.search-header{
    position: fixed;
    left: 0;
    top: 0;
    padding: 0.15rem 0.4rem 0.15rem 1rem;
    width: 100%;
    height: 0.9rem;
    box-sizing: border-box;
    background: #fff;
    z-index: 99;
  }
  .search-header .s-back{
    position: absolute;
    left: 0;
    top: 0;
    width: 0.44rem;
    height: 0.44rem;
    padding: 0.23rem;
  }
  .search-header .s-back img{
    width: 100%;
    height: 100%;
  }
  .search-box{
    position: relative;
    background: #f7f7f7;
    border-radius: 4px;
  }
  .search-box input{
    appearance: none;
    border: none;
    margin: 0;
    padding: 0;
    width: 100%;
    line-height: 0.4rem;
    border-radius: 4px;
    padding: 0.1rem 0.2rem 0.1rem 0.6rem;
    box-sizing: border-box;
    font-size: 14px;
    background: #f7f7f7;
  }
  .search-box .s-dot{
    position: absolute;
    left: 0.1rem;
    top: 0.1rem;
    width: 0.4rem;
  }
  .search-contain{
    padding-top: 0.9rem;
  }
  .list li{
    padding: 0 0.3rem;
    background: #fff;
    position: relative;
    margin-top: 0.1rem;
  }
  .list li a{
    display: block;
  }
  .list li .title{
    line-height: 0.6rem;
    position: relative;
    color: #999;
  }
  .list li .title .status{
    float: right;
    color: #98c347;
  }
  .list li .title:after{
    position: absolute;
    left: 0;
    bottom: -1px;
    width: 100%;
    height: 1px;
    border-top:solid 1px #eee;
    transform: scaleY(0.5);
    content: '';
  }
  .list li .con{
    display: flex;
    padding: 0.2rem 0;
    height: 1.5rem;
    position: relative;
  }
  .list li .con img{
    width: 1.5rem;
    height: 1.5rem;
    flex: 0 0 1.5rem;
  }
  .list li .con .dd{
    flex: 1;
    padding-left: 0.2rem;
  }
  .list li .con .dd h3{
    line-height: 0.4rem;
    font-size: 15px;
    font-weight: 500;
    height: 0.8rem;
    margin-top: 0.1rem;
    overflow-y: hidden;
  }
  .list li .con .dd p{
    color: #999;
    font-size: 15px;
  }
  .list li .con .dd p span{
    color: #e10000;
    font-weight: 500;
    float: right;
  }
  .list li .option{
    position: relative;
    padding: 0.2rem 0;
    text-align: right;
    font-size: 0;
    height: 0.5rem;
  }
  .list li .option:after{
    position: absolute;
    left: 0;
    top: -1px;
    width: 100%;
    height: 1px;
    border-top:solid 1px #ddd;
    transform: scaleY(0.5);
    content: '';
  }
  .list li .option .item{
    display: inline-block;
    width: 1.5rem;
    height: 0.5rem;
    margin-left: 0.2rem;
  }
  .list li .option .item span{
    display: block;
    width: 200%;
    height: 200%;
    font-size: 26px;
    border: solid 1px #a0c759;
    box-sizing: border-box;
    border-radius: 0.5rem;
    transform: scale(0.5) translate(-50%,-50%);
    text-align: center;
    line-height: 1rem;
    color: #a0c759;
  }
  .list li .option .item span.cancel{
    color: #999;
    border-color: #999;
  }
  .no-data{
    color: #999;
    text-align: center;
    line-height: 0.4rem;
    margin-top: 0.5rem;
  }
  .no-data{
    margin: 0.5rem;
    text-align: center;
    line-height: 0.4rem;
    color: #999;
  }
</style>


