global.webTip = function(txt,fn,time){
  var webTip=document.getElementById("webTip");
  if(webTip){
    return false;
  }
  var div=document.createElement("div");
  var body=document.getElementsByTagName("body")[0];
  var t;
  if(time){
    t=time;
  }else{
    t=1000;
  }
  div.style.cssText="position:fixed;left:50%;top:50%;padding:0.4rem 0.6rem;background:rgba(0,0,0,0.7);-webkit-transform:translate(-50%,-50%);font-size:15px;color:#fff;text-align:center;border-radius:0.08rem;z-index:999;-webkit-transition:all 0.3s;";
  div.innerHTML=txt;
  div.id="webTip";
  body.appendChild(div);
  setTimeout(function(){
    body.removeChild(div);
    if(fn) fn();
  },t)
}

global.inArray = function(word,Array){
  for(let i in Array){
    if(Array[i] == word){
      return true;
    }
  }
  return false;
}


//浏览器类型判断
var inBrowser = typeof window !== 'undefined';            
var UA = inBrowser && window.navigator.userAgent.toLowerCase();
global.isIOS = UA && /iphone|ipad|ipod|ios/.test(UA);  //是否IOS浏览器
global.isAndroid = UA && UA.indexOf('android') > 0;  //是否安卓浏览器


