import qs from 'qs'
import crypto from 'crypto'
export default{
  install(Vue,options)
  {
    //提交对象转换成字符串
    Vue.prototype.doQs = function(obj){
      if(typeof obj == 'object'){
        var str = qs.stringify(obj);
        return str;
      }else{
        console.log('请传入对象')
      }
    }

    //获取接口加密hash
    Vue.prototype.getHash = function(para){
      var keys = Object.keys(para);
      var newArray = keys.sort();
      var str='';
      for(var i = 0; i<newArray.length; i++){
        str = str + newArray[i] + para[newArray[i]];
      }
      var nStr = str + APIKEY ;
      return md5(nStr);

      function md5(str){    //jsmd5加密，返回加密结果
        var md5 = crypto.createHash("md5");
        md5.update(str);
        return md5.digest('hex');
      }
    }

    //返回上一页
    Vue.prototype.pageBack = function(){
      this.$router.go(-1);
      this.$router.isBack = true;
    },
    Vue.prototype.webTip = function(txt,fn,time){
      var webTip=document.getElementById("webTip");
      if(webTip){
        return false;
      }
      var div=document.createElement("div");
      var body=document.getElementsByTagName("body")[0];
      var t;
      if(time){
        t=time;
      }else{
        t=1000;
      }
      div.style.cssText="position:fixed;left:50%;top:50%;padding:0.4rem 0.6rem;background:rgba(0,0,0,0.7);-webkit-transform:translate(-50%,-50%);font-size:15px;color:#fff;text-align:center;border-radius:0.08rem;z-index:999;-webkit-transition:all 0.3s;";
      div.innerHTML=txt;
      div.id="webTip";
      body.appendChild(div);
      setTimeout(function(){
        body.removeChild(div);
        if(fn) fn();
      },t)
    }
  }
}



